*{
  box-sizing: border-box;
}

body{
  margin:0;
  padding:0;
}

.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

[class*="col-"]{
  float:left;
  padding:5px;
  border:1px solid red;
}

.row::after{
  content:"";
  clear:both;
  display:table;

}

@media screen and (min-width: 768px) {
  body {
    background-color: lightgreen;
  }
}

/* HEADER
-------------------------------------------- */

.header-container{
  background-color: rgb(25, 24, 38);
  height:104px;
  border-bottom: 2px solid;
  border-bottom-color:rgb(222,198,117);

}

.gamelist-text{
  display:block;
  margin-top:24px;
  font-size:22px;
  text-align:center;
  color:white;
  
}

.arrow{
  width:40px;
  display:block;
  margin-left:auto;
  margin-right:auto;
}

.logo{
  padding-top:0px;
  display:block;
  margin-left:auto;
  margin-right:auto;
  
}

.loginText{
  display:block;
  text-align: center;
  font-size:22px;
  color:white;
  line-height: 40px;
}

.signupText{
  display:block;
  text-align:center;
  font-size:15px;
  color:white;
}


/* NAV
==================================================== */


.nav-container{
  background-color: royalblue;
  height:50px;
}